.arrows {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: absolute;
  top: calc(50% - 25px);
  left: 0;

}

.slider {
  position: relative;
  z-index: 20;
  &__wrapper {
    width: 100%;
    position: relative;
  }

  &__slide {
    overflow: hidden;
  }
}

